
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast
},
mounted () {

window.onerror = function (message) {
alert(message)
}
},
data () {
return {
title: '注 册',
username: '',
mobile: '',
email: '',
password: '',
repeatPassword: '',
iconType: '',
failureText: '',
successText: '',
successShow: false,
changeFailureShow: false,
showWrongPwd: false,
loginSubmit: '登 录',
signUpSubmit: '注 册',
signUpLink: '',
forgetPwdLink: '',
LoginDisable: false,
isDisabled: true
}
},
methods: {
change () {
if (this.username && this.password && this.repeatPassword && this.email) {
this.isDisabled = false
} else {
this.isDisabled = true
}
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
onHide () {
this.$router.push('/')
},
registerAction () {
this.isDisabled = true

if (this.password !== this.repeatPassword) {
this.showWrongPwd = true
} else {
this.signUpSubmit = 'Loading...'
this.showWrongPwd = false
let parmas = {
username: this.username,
password: this.password,
email: this.email
}
this.$http.post(this.$apis.register, parmas)
.then(res => {
if (res.code === 200) {
this.successShow = true
this.successText = res.msg
} else {
this.changeFailureShow = true
this.failureText = res.msg
this.isDisabled = false
}
this.signUpSubmit = '注 册'
})
.catch((error) => {
console.log(error)
this.signUpSubmit = '注 册'
})
}
}
}
}

window.onError = function (message) {
alert(message)
}



